@use 'styles/sass' as *;

.tree-card {
  padding: get-unit(2);
  border-radius: $border-radius;

  position: relative;

  &__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: bold;
    color: get-color('white');
    text-transform: uppercase;
    font-family: 'Ubuntu Mono', monospace;
    text-align: center;
    font-size: 1.25em;
    @include text-stroke;
  }
}
